.main-container {
  position: absolute;
  top: 5%;
  left: 5%;
  width: 90%;
  height: 90vh;
  background-color: #9ACEEB;
  border: 1px solid black;
  border-radius: 15px;
  opacity: 0.5;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cheking-my-cow {
  width: 40%;
  min-height: 40%; 
  padding: 10px;
  margin: 20px;
  border-radius: 15px;
  background-color: #6c95d3;
  border: 1px solid black;
  flex-grow: 1;
}

.cheking-enemy-cow {
  width: 40%;
  min-height: 40%; 
  padding: 10px;
  margin: 20px;
  border-radius: 15px;
  background-color: #6c95d3;
  border: 1px solid black;
  flex-grow: 1;
}

.input-my-number {
  text-align: center;
  width: calc(5% + 20px);
  height: 40px;
  border-radius: 5px;
  margin: 1px;
  font-size: 24px;
  padding-left: 9px;
  margin-bottom: 20px;
}

.input-checked-number {
  text-align: center;
  width: calc(5% + 10px);
  height: 30px;
  border-radius: 5px;
  font-size: 24px;
  margin: 1px;
}

.result-cheked-number {
  display: inline;
  text-align: center;
  width: calc(4% + 5px);
  height: 30px;
  font-size: 30px;
  align-self: flex-end;
}

.cow-number,
.bull-number {
  margin-left: 5px;
  margin-right: 4px;
  text-align: center;
  width: calc(5% + 5px);
  height: 30px;
  border-radius: 5px;
  font-size: 24px;
}


.cheking-enemy-cow {
  font-size: 30px;
}

.cheking-enemy-cow .input-checked-number,
.cheking-enemy-cow .cow-number,
.cheking-enemy-cow .bull-number {
  text-align: center;
  width: calc(5% + 20px);
  height: 40px;
  border-radius: 5px;
  font-size: 24px;
  margin: 1px;
  padding-left: 9px;
  margin-bottom: 10px;
}


.cheking-enemy-cow .cow-number,
.cheking-enemy-cow .bull-number {
  margin: 10px;
  margin-top: 0px;
  opacity: 0.3;
}